import Vue from "vue";
export default {

	spBus: {},

	getSpBus: function() {
		if (!this.isNotEmpty(this.spBus)) {
			this.spBus = new Vue();
			this.spBus.spId = "creditload";
		}
		return this.spBus;
	},

	getRequest: function() {
		let url = location.search; //获取url中"?"符后的字串
		let theRequest = new Object();
		if (url.indexOf("?") != -1) {
			let str = url.substr(1);
			let strs = str.split("&");
			for (let i = 0; i < strs.length; i++) {
				theRequest[strs[i].split("=")[0]] = unescape(strs[i].split("=")[1]);
			}
		}
		return theRequest;
	},

	isNotEmpty: function(Arr) {

		if (!Arr || Arr == '' || Arr == null || Arr == 'undefined' || Arr == undefined || Arr.length == 0) {
			return false;
		}
		return true;

	},

	isNotEmptyObject: function(obj) {
		for (let key in obj) {
			return true;
		}
		return false;
	},


	getcookie: function(objName) {
		let cookieText = "";
		let cookie_start = document.cookie.indexOf(objName);
		let trim = function(str) {
			return str.replace(/(^\s*)|(\s*$)/g, "");
		};
		if (cookie_start != -1) {
			let arrStr = document.cookie.split(";");
			for (let i = 0; i < arrStr.length; i++) {
				let temp = arrStr[i].split("=");
				if (trim(temp[0]) == objName) {
					cookieText = window.unescape(temp[1]);
				}
			}
		}
		return cookieText;
	},


	setcookie: function(cookieName, cookieValue, seconds, path, domain, secure) {
		let expires = new Date();
		expires.setTime(expires.getTime() + seconds);
		document.cookie = escape(cookieName) + '=' + escape(cookieValue) + (expires ? '; expires=' + expires.toGMTString() : '') + (path ? '; path=' + path : '/') + (domain ? '; domain=' + domain : '') + (secure ? '; secure' : '');
	},

	getOffsetTop: function(ele) {
		let box = ele.getBoundingClientRect();
		let body = document.body,
			docElem = document.documentElement;
		//获取页面的scrollTop,scrollLeft(兼容性写法)
		let scrollTop = window.pageYOffset || docElem.scrollTop || body.scrollTop;
		let clientTop = docElem.clientTop || body.clientTop;
		let top = box.top + scrollTop - clientTop;
		//Math.round 兼容火狐浏览器bug
		return Math.round(top);
	},


	getScrollTop: function() {
		let scrollTop = 0,
			bodyScrollTop = 0,
			documentScrollTop = 0;
		if (document.body) {
			bodyScrollTop = document.body.scrollTop;
		}

		if (document.documentElement) {
			documentScrollTop = document.documentElement.scrollTop;
		}

		scrollTop = (bodyScrollTop - documentScrollTop > 0) ? bodyScrollTop : documentScrollTop;
		return scrollTop;
	},

	loadScript: function(src, callback) {
		let script = document.createElement('script');
		script.type = 'text/javascript';
		script.src = src;
		script.onload = function() {
			if (callback && callback instanceof Function) {
				callback();
			}
		};
		document.body.appendChild(script);
	},


}


