<template>

  <div class="firstPage">

    <pull-down ></pull-down>
    <div class="pull-down-main">
      <p class="title" v-on:click="handlePage()">firstPage</p>
      <p>随机数：<span>{{randomNum}}</span></p>

      <br/>
      <p>测试1px</p>
      <div class="borderTest"></div>
      <br/>
      <div class="borderTestRem"></div>

      <br/>
      <p>测试icon</p>
      <i class="sprite1"></i>
      <i class="sprite2"></i>

      <br/>
      <p>测试多个px写法</p>
         <div class="padingtest">测试padding</div>


      <p>测试pullDown</p>
      <p>测试pullDown</p>
      <p>测试pullDown</p>
      <p>测试pullDown</p>
      <p>测试pullDown</p>
      <p>测试pullDown</p>
      <p>测试pullDown</p>
      <p>测试pullDown</p>
      <p>测试pullDown</p>
      <p>测试pullDown</p>
      <p>测试pullDown</p>
      <p>测试pullDown</p>
      <p>测试pullDown</p>
      <p>测试pullDown</p>
      <p>测试pullDown</p>
      <p>测试pullDown</p>
  </div>
  </div>
</template>


<script>
import pullDown from "@/components/pullDown/pullDown.vue";
import "./firstPage.scss";
export default {
  data() {
    // console.log("---------- data -----------");
    let self = this;

    return {
      randomNum: "0000",

      ss: []
    };
  },
  props: {},
  created() {
    // console.log("---------- created -----------");
  },
  updated() {
    // console.log("---------- updated -----------");
  },
  components: {
    // console.log("---------- components -----------");
    pullDown
  },
  activated() {
    console.log("缓存的组件又出现了，我是不是要重新拉取数据呢？");
  },
  mounted() {
    console.log("---------- mounted -----------");
    let self = this;

    // 调用接口
    self.$store
      .dispatch("firstPage/fetchRandomNum")
      .then(response => {
        //* 根据服务器返回数据
        self.randomNum = self.$store.getters["GrandomNum"];
      })
      .catch(err => {
        self.randomNum = "接口出错";
      });
  },

  watch: {},

  methods: {
    handlePage() {
      this.$router.push("/secondPage");
    },
    handleNoop() {
      //noop
    }
  }
};
</script>



