<template>
	<div class="pull-down">
	  <i class="icon-arrow" v-if="arrowMoreFlag"></i>
	  <i class="icon-more" v-if="!arrowMoreFlag"></i>
	  <span class="icon-text">{{iconText}}</span>
	</div>
</template>

<script>

	import "./pullDown.scss";
	import crlUtil from "@/crlUtil.js";
	export default {
        data() {
            let self = this;
            return {
                arrowMoreFlag: true,
                iconText: "下拉刷新"
            }
        },
        props: {},

        created() {
            // console.log("---------- created-----------");
        },
        updated() {
            // console.log("---------- updated-----------");
        },
        mounted() {
            // console.log("---------- mounted-----------");
            let self = this;

            self.mainEl = document.getElementsByClassName("pull-down-main")[0]; //container
            self.downEl = document.getElementsByClassName("pull-down")[0]; //pull-down banner
            self.arrowEl = document.getElementsByClassName("icon-arrow")[0]; //arrow
            self.loadMoreEl = document.getElementsByClassName("icon-more")[0]; //more 

            self.maxDownLoadPull = self.getViewportSize().height * 0.75; //最大下拉位置
            self.startY = -1; //初始位置

            self.downElClientHeight = self.downEl.clientHeight; //移动最大高度
            self.criticalPos = self.downElClientHeight * 0.9; //移动临界值
            self.mainYPosition = 0; //实时移动的位置偏移量

            self.timeClick = null; //时间函数

            self.arrowDirection = true; //true 向下 || false 向上

            self.callbackFun = "";
            self.preventDebounce = true;

            document.addEventListener('touchstart', self.pulldowntouchstart, false);
            document.addEventListener('touchmove', self.pulldowntouchmove, false);
            document.addEventListener('touchend', self.pulldowntouchend, false);
            document.addEventListener('webkitTransitionEnd', self.pulldowntransitionEnd, false);
        },

        watch: {},

        methods: {

            pulldowntouchstart(e) {
                let self = this;

                if (self.getScrollTop() > 1) return;

                clearTimeout(self.timeClick);
                self.startY = e.targetTouches[0].clientY;
            },

            pulldowntouchmove(e) {
                let self = this;

                if (self.getScrollTop() > 1) return;

                let eachMoveY = e.targetTouches[0].clientY - self.startY;

                if (eachMoveY > 0) { //只有向下拉的时候 才进行
                    self.setMainTop(eachMoveY, e);
                }

                self.startY = e.targetTouches[0].clientY;

            },

            setMainTop(eachMoveY, e) {
                let self = this;

                if (self.mainYPosition <= self.maxDownLoadPull && self.mainYPosition >= 0) {

                    self.lockScreen(e);

                    self.mainYPosition = self.mainYPosition + eachMoveY;
                    self.mainYPosition = Math.min(self.maxDownLoadPull, self.mainYPosition);
                    self.mainYPosition = Math.max(self.mainYPosition, 0);

                    let pos = self.mainYPosition - self.downElClientHeight;

                    self.setStaticPos(pos);

                    if (self.mainYPosition > self.criticalPos && self.arrowDirection) {

                        self.arrowEl.style["webkitTransform"] = 'rotate(180deg)';
                        self.arrowEl.style["transform"] = 'rotate(180deg)';
                        self.iconText = "释放刷新";
                    }

                }
            },

            pulldowntouchend(e) {
                let self = this;

                if (self.getScrollTop() > 1) return;

                if (self.mainYPosition > self.criticalPos) {

                    self.arrowMoreFlag = false;
                    self.iconText = "正在加载";

                    //先回到顶部
                    self.mainYPosition = self.downElClientHeight;
                    self.setStaticPos(0);

                    //TODO 回调函数
                    self.timeClick = setTimeout(() => {
                        self.setZeroState();
                    }, 1000);

                } else {
                    self.setZeroState();
                }

            },

            setZeroState() {
                let self = this;

                let pos = -self.downElClientHeight;

                /****** 关键数据清 0 ******/
                self.mainYPosition = 0;
                self.arrowDirection = true;

                self.arrowMoreFlag = true;

                /****** 关键数据清 0 ******/

                self.setStaticPos(pos);

                self.arrowEl.style["webkitTransform"] = 'rotate(0deg)';
                self.arrowEl.style["transform"] = 'rotate(0deg)';

            },

            setStaticPos(pos) {
                let self = this;
                self.downEl.style["webkitTransform"] = 'translate3d(0, ' + pos + 'px, 0)';
                self.downEl.style["transform"] = 'translate3d(0, ' + pos + 'px, 0)';
                self.mainEl.style["webkitTransform"] = 'translate3d(0, ' + pos + 'px, 0)';
                self.mainEl.style["transform"] = 'translate3d(0, ' + pos + 'px, 0)';
            },

            setMoveState(pos) {
                let self = this;

                if (self.preventDebounce) {
                    self.preventDebounce = false;
                    downEl.style["webkitTransform"] = 'translate3d(0, ' + pos + 'px, 0)';
                    downEl.style["transform"] = 'translate3d(0, ' + pos + 'px, 0)';

                    mainEl.style["webkitTransform"] = 'translate3d(0, ' + pos + 'px, 0)';
                    mainEl.style["transform"] = 'translate3d(0, ' + pos + 'px, 0)';
                }
            },

            pulldowntransitionEnd(e) {
                let self = this;
                self.preventDebounce = true;
            },

            getScrollTop() {
                var scrollPos;
                if (window.pageYOffset) {
                    scrollPos = window.pageYOffset;
                } else if (document.compatMode && document.compatMode != 'BackCompat') {
                    scrollPos = document.documentElement.scrollTop;
                } else if (document.body) {
                    scrollPos = document.body.scrollTop;
                }
                return scrollPos;
            },
            
            getViewportSize() {
                return {
                    width: window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth,
                    height: window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
                };
            },

            lockScreen(e) {
                e.preventDefault();
                e.stopPropagation();
            },



            handleNoop: function() {
                //noop
            },
        },

    }
</script>



